body {
  
    font-family: $font-family-Roboto;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 1rem;
    color: #000;
    margin:0;
    padding:0;
 
      
    scroll-behavior: smooth;
    ::-webkit-scrollbar {
      width: 12px;               /* ширина scrollbar */
    }
    ::-webkit-scrollbar-track {
      background: $green;        /* цвет дорожки */
    }
    ::-webkit-scrollbar-thumb {
      background-color: $gray-light;    /* цвет плашки */
      border-radius: 20px;       /* закругления плашки */
      border: 3px solid $green;  /* padding вокруг плашки */
    }  
  }
  a:hover{
    transition: all 0.2s ease-in-out;
    color: $green;
  }
  
  .container{
    max-width: 100%;
    margin: 0 auto;
    &--s{
      max-width: rem(560);
    }
  } 

  
  h1{
    font-style: normal;
    font-weight: bold;
    @include adaptiv-value("font-size", 70, 40, 1);
    @include adaptiv-value("line-height", 66, 30, 1);  
  }
  h2{ 
    font-style: normal;
    font-weight: 500;
    @include adaptiv-value("font-size", 40, 20, 1);
    @include adaptiv-value("line-height", 47, 23, 1);  
  }
  h3{ 
    font-style: normal;
    font-weight: 500;
    @include adaptiv-value("font-size", 32, 16, 1);
    @include adaptiv-value("line-height", 37, 18, 1); 
  }
  h4{
    font-style: normal;
    font-weight: bold;
    @include adaptiv-value("font-size", 30, 14, 1);
    @include adaptiv-value("line-height", 35, 16, 1); 
  }
  
  h5{}
  
  h6{
    font-style: normal;
    font-weight: normal;
    @include adaptiv-value("font-size", 13, 8, 1);
    @include adaptiv-value("line-height", 15, 9, 1); 
  }
  
  /* color themes */
  .color{
    &--green{
      color: $green;
    }
    &--black{
      color: $black;
    }
  }